import apiFetch from "@wordpress/api-fetch"
import { Button, PanelBody, PanelRow } from "@wordpress/components"
import { RichText, InnerBlocks, InspectorControls, MediaUpload, MediaUploadCheck } from "@wordpress/block-editor"
import { registerBlockType } from "@wordpress/blocks"
import { useEffect } from "@wordpress/element"

registerBlockType("ourblocktheme/singleproject-image-and-text", {
  title: "Project Detail - Image & Text",
  supports: {
    align: ["full"]
  },
  attributes: {
    align: { type: "string", default: "full" },
    text: { type: "string" },
    imgID: { type: "number" },
    imgURL: { type: "string", default: people.base_url + 'asset/image/home-hero.jpg' },
  },
  edit: EditComponent,
  save: SaveComponent
})

function EditComponent(props) {
  useEffect(
    function () {
      if (props.attributes.imgID) {
        async function go() {
          const response = await apiFetch({
            path: `/wp/v2/media/${props.attributes.imgID}`,
            method: "GET"
          })
          console.log(response.media_details.sizes)
          props.setAttributes({ imgURL: response.media_details.sizes.full.source_url })
        }
        go()
      }
    },
    [props.attributes.imgID]
  )

  function onFileSelect(x) {
    props.setAttributes({ imgID: x.id })
  }

  function handleTextChange(x) {
    props.setAttributes({ text: x })
  }

  return (
    <>
      <InspectorControls>
        <PanelBody title="Image" initialOpen={true}>
          <PanelRow>
            <MediaUploadCheck>
              <MediaUpload
                onSelect={onFileSelect}
                value={props.attributes.imgID}
                render={({ open }) => {
                  return <Button onClick={open}>Choose Image</Button>
                }}
              />
            </MediaUploadCheck>
          </PanelRow>
        </PanelBody>
      </InspectorControls>
      <div className="alfk_project_detail_content prd_sc9 reveal hover_mouse">
          <div className="alfk_box_left sh_1328 text">
            <div className="alfk_box_top">
              <RichText allowedFormats={["core/bold", "core/italic"]} tagName="p" value={props.attributes.text} onChange={handleTextChange} placeholder="Input Text here" />
            </div>
          </div>
          <div className="alfk_box_right sh_1328 text">
              <div className="popup_image">
                  <div className="alfk_box_top sh_797">
                      <img className="text" src={`${props.attributes.imgURL}`} />
                  </div>
                  <button className="apply_career apply_pop" data-id="pop_index_1">
                      <span className="btn-plus-pop"></span>
                  </button>
              </div>                        
          </div>
      </div>
    </>
  )
}

function SaveComponent() {
  return <InnerBlocks.Content />
}
